body {
    padding: 0 !important;
}

.App {
    height: 100vh;
    width: 100%;

    &__header {
        z-index: 2;
        position: fixed;
        width: 100%;
        background-color: #0E2841;
    }

    &__content {
        transform: translateY(75px);
        min-height: calc(100% - 75px);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
}